@import url(https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap);
body {
  background-color: rgb(243, 236, 236);
}
.blog__page {
    padding: 30px 170px;
  }
  
  .blog__page__header {
    letter-spacing: -1px;
    font-size: 35px;
  }
  
  .blogs {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 50px;
    padding: 40px 0;
  }
  
  .blog {
    min-width: 300px;
    margin: auto;
    background: white;
    box-shadow: 0px 0px 50px rgba(0, 0, 0, 0.1);
    height: 100%;
    text-decoration: none;
    color: black;
    border-radius: 10px;
    border: 2px solid black;
    box-shadow: 0 1px 2px rgba(0,0,0,0.15);
    transition: all 0.3s ease-in-out;
  }
  .blog:hover {
    box-shadow: 0 5px 15px rgba(0,0,0,0.3);
    transition: opacity 0.3s ease-in-out;
    -webkit-transform: scale(1.2, 1.2);
            transform: scale(1.2, 1.2);
  }
  
  .blog > div {
    margin-top: 15px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    padding: 0px 30px 0px 30px;
  }
  
  .blog > img {
    width: 100%;
  }
  
  .sourceName > span {
    display: inline;
    background: rgba(38, 0, 128, 0.226);
    font-size: 11px;
    padding: 5px 6px;
    color: rgba(0, 11, 43, 0.534);
    border-radius: 15px;
  }
  
  .sourceName > p {
    font-size: 10px;
    font-weight: 500;
    padding-left: 10px;
  }
  
  .sourceName {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
  }
  
  .blog > div > h1 {
    font-size: 25px;
    letter-spacing: -0.6px;
    line-height: 1.3em;
    padding: 10px 0;
    font-weight: 700;
    text-decoration: underline;
  }
  
  .blog > div > p {
    font-size: 13px;
    line-height: 1.6em;
    color: rgb(90, 90, 90);
    padding-bottom: 30px;
  }
  
  .no__blogs {
    font-size: 25px;
    font-weight: 500;
    line-height: 1.5em;
  }
.home__page {
    height: 87vh;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    width: 50%;
    margin: 0 auto;
    text-align: center;
    background-color: rgb(232, 236, 243);
   
  }
  
  .login__message {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
  }
  
  .login__message > h2 {
    font-size: 80px;
    margin: 0;
  }
  
  .login__message > h3 {
    font-size: 100px;
    width: 80%;
    line-height: 1em;
    margin: 10px auto;
    margin-top: 0;
    font-weight: 900;
    letter-spacing: -4px;
    color: rgb(5, 5, 5);
    text-decoration: underline;
  }
  
  .login__message > p {
    padding: 15px 0;
    line-height: 1.5em;
    width: 50%;
    margin: auto;
    font-size: 16px;
  }
  
  .login__button {
    width: 300px;
    margin: 15px auto 0px auto;
    padding: 15px 0;
    border: none;
    background: rgb(46, 3, 238);
    font-family: "Inter";
    color: white;
    font-size: 15px;
    font-weight: 500;
    cursor: pointer;
    border-radius: 10px;
  }
.navbar {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    height: 13vh;
    -webkit-align-items: center;
            align-items: center;
    padding: 0px 80px;
    background: white;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
    background: #020202;
    color: white;
  }
  
  .navbar__header {
    font-size: 20px;
    font-weight: 700;
  }
  
  .navbar__user__data {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
  }
  
  .user {
    cursor: pointer;
    border: 2px solid rgb(0, 255, 234);
  }
  
  .signedIn {
    font-size: 15px;
    padding-left: 10px;
    cursor: pointer;
  }
  
  .logout__button {
    margin-left: 20px;
    padding: 12px 25px;
    border-radius: 50px;
    border: none;
    background: rgb(39, 39, 39);
    font-family: "Inter";
    color: white;
    font-size: 14px;
    font-weight: 500;
    cursor: pointer;
  }
  
  .notSignedIn {
    font-size: 15px;
  }
  
  .blog__search {
    -webkit-flex: 1 1;
            flex: 1 1;
    width: 100%;
    display: -webkit-flex;
    display: flex;
    padding: 10px 0px;
    margin: 0px 70px;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    background: rgb(39, 39, 39);
    border-radius: 5px;
  }
  
  .search {
    width: 80%;
    padding: 5px 30px;
    padding-left: 30px;
    background: none;
    font-family: "Inter";
    border: none;
    outline: none;
    font-size: 15px;
    color: white;
  }
  
  .submit {
    width: 18%;
    font-family: "Inter";
    background: none;
    color: white;
    font-size: 14px;
    border: none;
    cursor: pointer;
    margin-right: 10px;
    padding: 12px 0px;
    border-radius: 5px;
    transition: 0.1s ease-in-out;
    background: rgb(31, 31, 31);
    outline: none;
  }
  
  .submit:hover {
  }

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Inter", sans-serif;
}
